.movies-container{
    width: 99%;
    height: calc(100vh - 50px);
   
    overflow-y: scroll;
    z-index: 22;
    background-color: transparent;
    position: absolute;
    top: 50px;
    left: 10px;
    color: white;
    
}
.box {
    width: 100%;
    height: 100vh;
    z-index: -21;
    background: linear-gradient(180deg, rgba(2,0,36,1) 0%, rgb(126, 47, 80) 35%, rgb(10, 1, 6) 100%);
    }
    h2 {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    color: #fff;
    
    font-weight: 900;
    text-transform: uppercase;
    font-size: 60px;
    }
    .box div {
    height: 60px;
    width: 60px;
    position: absolute;
    top: 10%;
    left: 10%;
    -webkit-animation: animate 4s linear infinite;
            animation: animate 4s linear infinite;
    background: rgba(255,255,255,.5);
    border-radius: 15px;
    }
    .box div:nth-child(1) {
    top: 20%;
    left: 20%;
    -webkit-animation: animate 8s linear infinite;
            animation: animate 8s linear infinite;
    }
    .box div:nth-child(2) {
    top: 26%;
    left: 89%;
    -webkit-animation: animate 10s linear infinite;
            animation: animate 10s linear infinite;
    }
    .box div:nth-child(3) {
    top: 80%;
    left: 90%;
    -webkit-animation: animate 5s linear infinite;
            animation: animate 5s linear infinite;
    }
    .box div:nth-child(4) {
    top: 65%;
    left: 75%;
    -webkit-animation: animate 7s linear infinite;
            animation: animate 7s linear infinite;
    }
    .box div:nth-child(5) {
    top: 90%;
    left: 10%;
    -webkit-animation: animate 9s linear infinite;
            animation: animate 9s linear infinite;
    }
    .box div:nth-child(6) {
    top: 30%;
    left: 60%;
    -webkit-animation: animate 5s linear infinite;
            animation: animate 5s linear infinite;
    }
    .box div:nth-child(7) {
    top: 70%;
    left: 33%;
    -webkit-animation: animate 8s linear infinite;
            animation: animate 8s linear infinite;
    }
    .box div:nth-child(8) {
    top: 75%;
    left: 60%;
    -webkit-animation: animate 10s linear infinite;
            animation: animate 10s linear infinite;
    }
    .box div:nth-child(9) {
    top: 23%;
    left: 50%;
    -webkit-animation: animate 6s linear infinite;
            animation: animate 6s linear infinite;
    }
    .box div:nth-child(10) {
    top: 35%;
    left: 7%;
    -webkit-animation: animate 10s linear infinite;
            animation: animate 10s linear infinite;
    }
    @-webkit-keyframes animate {
    0% {
    -webkit-transform: scale(0) translateY(0) rotate(50deg);
            transform: scale(0) translateY(0) rotate(50deg);
    }
    100% {
    -webkit-transform: scale(1.6) translateY(-250px) rotate(360deg);
            transform: scale(1.6) translateY(-250px) rotate(360deg);
    }
    }
    @keyframes animate {
    0% {
    -webkit-transform: scale(0) translateY(0) rotate(50deg);
            transform: scale(0) translateY(0) rotate(50deg);
    }
    100% {
    -webkit-transform: scale(1.6) translateY(-250px) rotate(360deg);
            transform: scale(1.6) translateY(-250px) rotate(360deg);
    }
    }

