.movieTableRow{
    width: 100%;
    height: calc(100%/6.1);
    border-bottom: 1px solid lightgrey;
    display: flex;
    justify-content: space-around;
    align-items: center;
    transition: all 0.5s ease;

}
.column{
    text-align: center;
    width:calc(100%/4);
    
    
}
